.cm-gutters { margin: 1px; }
.cm-scroller { overflow: auto; }
.cm-wrap { border: 1px solid silver }
.cm-editor { width: 100%; box-sizing: border-box;
 }

.friendly .layout {
  display: flex;
  /*flex-wrap: wrap;*/
  width: 100%; 
  box-sizing: border-box;
}

html, body, .friendly .layout {
margin: 0px;
padding: 0px;
}

@media only screen and (max-width: 600px) {
.friendly #editorsection {
 width: 100%; 
 box-sizing: border-box;
  margin: 0px;
  padding: 0px;
}
  .friendly .layout {
  flex-wrap: wrap;
  }

  .friendly #guidesection {
    min-width: 20rem;
    height: 6rem;
    overflow: scroll;
  }
  
.friendly #output {
  min-width: 20rem;
  width: 100%; 
  box-sizing: border-box;
  margin: 0px;
  padding: 0px;
}

}

@media only screen and (min-width: 600px) {

 .friendly #editorsection {
   width: 35rem;
  margin: 0px;
  padding: 0px;
}

  
  .friendly #output {
  min-width: 20rem;
  width: 100%; 
  margin: 0px;
  padding: 0px;
  height: 100%;


     body {
    overflow-y: scroll;
  } 
}

.friendly #guidesection {
  max-height: 95vh;
  min-width: 20rem; 
}

.friendly #editorsection .cm-editor{
}

.friendly #editorsection {
  max-height: 95vh;
}

.friendly #outputsection {
  flex-grow: 6;
}

.friendly #output {
   /*width: 40rem;*/
}

.friendly #topbar {
  width: 100%; 
  box-sizing: border-box;
  margin: 0px;
  padding: 0px;
}

.friendly #guidesection {
  width: 20rem;
}

.friendly button {
  vertical-align: top;
  height: 1.5rem;
  line-height: 1.5rem;
  margin: 0;
  text-align: center;
}

.friendly #editor .cm-indentation-marker, .friendly #editor, .friendly #csseditor {
  border-left: 3px solid rgba(0, 136, 85, 0.5);
}

.friendly #editor .cm-editor, .friendly 
 #csseditor .cm-editor {
  height: 18rem;
  caret-color: mediumvioletred !important;
}

.friendly .tutorial .code {
  box-shadow: 1px 1px 8px -2px #000000;
  width: 90%;
}

.csscolor {
  margin: 0 auto;
}

.chooseoption {
  margin: 0 auto;
  visibility: visible;
  display: block;
  font-family: monospace;
} 

.friendly .snippet .cm-scroller {
  overflow: hidden;
}

.friendly .cm-activeLine {
  font-size: 1.1rem;
}


